h1,
h2,
h3,
h4,
h5,
h6,
.giant-heading {
    font-family: $alt-family;
    line-height: 1.3;
    margin-bottom: 32px;
    letter-spacing: -0.03em;
}

.giant-heading {
    font-size: 74px;
}

h1,
.h1 {
    font-size: 58px;
}

h2,
.h2 {
    font-size: 46px;
}

h3,
.h3 {
    font-size: 36px;
}

h4,
.h4 {
    font-size: 28px;
}

h5,
.h5 {
    font-size: 26px;
}

h6,
.h6 {
    font-size: 23px;
}

.title__orange-bg {
    padding: 12px;
    background-color: $orange;
    color: #fff;
    box-decoration-break: clone;
    font-family: $alt-family, sans-serif;
}
.title__blue-bg {
    padding: 12px;
    background-color: $blue;
    color: #fff;
    box-decoration-break: clone;
    font-family: $alt-family, sans-serif;
}

p,
b,
a,
li,
span {
    font-family: $standard-family;
    font-size: 16px;
    line-height: 1.55;
    font-weight: normal;
}

p {
    margin-bottom: 24px;
}

.p-giant {
    font-size: 32px;
}

.p-big {
    font-size: 26px;
}

.p-mid {
    font-size: 20px;
}

.p-small {
    font-size: 12px;
}

.p-micro {
    font-size: 10px;
}

a {
    text-decoration: none;
    &:hover {
        text-decoration: none;
    }
}

.text__gray-bg {
    background-color: $lighter-gray;
    padding: 64px 32px 32px;
    font-size: 20px;
    line-height: 30px;
    color: $dark_gray;
    font-family: 'DM Sans', sans-serif;
    font-weight: 400;
}

.single-post {
    .entry-content {
        a {
            font-weight: bold;
        }
    }
}

.entry-content {
    font-size: 16px;
    line-height: 1.33;

    * {
        &:first-child {
            margin-top: 0;
        }
    }

    a {
        color: $orange;
        text-decoration: underline;
        -webkit-tap-highlight-color: transparent;
        transition: color 0.2s ease;

        &:hover {
            text-decoration: underline;
        }
    }

    p {
        margin-bottom: 16px;

        .featuredCTA & {
            margin-bottom: 4px;
        }
    }

    ul,
    ol {
        margin-bottom: 16px;
        padding-left: 32px;

        > li {
            margin-bottom: 8px;

            &:last-of-type {
                margin-bottom: 0;
            }
        }
    }

    ol {
        list-style-type: decimal;
    }

    ul {
        list-style-type: disc;

        > li {
            > ul {
                list-style-type: circle;
                margin-bottom: 0;
            }
        }
    }

    strong,
    b {
        font-weight: 700;
    }

    em,
    i {
        font-style: italic;
    }

    img {
        max-width: 100%;
        height: auto;
    }

    .alignnone {
        margin: 16px 0;
    }

    p img.alignnone {
        margin-bottom: 0;
    }

    hr {
        border: 0;
        border-bottom: 1px solid rgba(73, 89, 102, 0.2);
        margin: 20px 0;
    }

    .table__container {
        position: relative;
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
        margin-bottom: 16px;
    }

    table {
        width: 100%;
        border-collapse: collapse;
        border: 1px solid #d3d3d3;
    }

    th,
    td {
        padding: 16px;
        text-align: center;
        vertical-align: middle;

        @media #{$phone} {
            font-size: 16px;
        }

        em,
        i {
            font-weight: 400;
            font-size: 16px;

            @media #{$phone} {
                font-size: 14px;
            }
        }
    }

    th {
        font-weight: 600;
        background-color: #e8e8e8;

        &:first-child {
            text-align: left;
        }
    }

    td {
        background-color: #f1f1f1;
    }

    tbody {
        tr {
            &:nth-child(even) {
                td {
                    background-color: #d6d6d6;
                }
            }
        }
    }
}

::-webkit-input-placeholder {
    /* Chrome/Opera/Safari */
    color: #777777;
}
::-moz-placeholder {
    /* Firefox 19+ */
    color: #777777;
}
:-ms-input-placeholder {
    /* IE 10+ */
    color: #777777;
}
:-moz-placeholder {
    /* Firefox 18- */
    color: #777777;
}
